<template>
  <div class="layout">
    login
    <a href="#" @click="login">登陆</a>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    created () {
      console.log(this.$store.state.isLogin)
    },
    methods: {
      login () {
        this.$store.state.isLogin = true
      }
    }
  }
</script>

