import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import iView from 'iview'

import Admin from 'components/admin/admin'
import Login from 'components/login/login'
import My from 'components/my/my'
import Work from 'components/work/work'
import Bug from 'components/bug/bug'
import File from 'components/file/file'
import Weekly from 'components/weekly/weekly'
import System from 'components/system/system'
import User from 'components/user/user'
import Err404 from 'components/error/404'

Vue.use(Router)
Vue.use(Resource)
Vue.use(iView)

export default new Router({
  // mode: 'history',
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      meta: { auth: false },
      redirect: '/admin/index'
    },
    {
      path: '/admin',
      name: 'admin',
      meta: { auth: true },
      component: Admin,
      children: [
        { path: 'index', meta: { auth: true }, component: My },
        { path: 'work', meta: { auth: true }, component: Work },
        { path: 'bug', meta: { auth: true }, component: Bug },
        { path: 'file', meta: { auth: true }, component: File },
        { path: 'weekly', meta: { auth: true }, component: Weekly },
        { path: 'system', meta: { auth: true }, component: System },
        { path: 'user', meta: { auth: true }, component: User }
      ]
    },
    {
      path: '/login',
      name: 'login',
      meta: { auth: false },
      component: Login
    },
    {
      path: '*',
      meta: { auth: false },
      component: Err404
    }
  ]
})



